import React, { FC } from "react";

import { Avatar } from "../Avatar";
import { colors } from "../styleguide";
import Typography from "../Typography/Typography";

import {
	ContentWrapper,
	MediaWrapper,
	StyledMediaObject,
	Thumbnail,
} from "./MediaObject.styles";
import { MediaObjectProps } from "./MediaObject.types";

const MediaObject: FC<MediaObjectProps> = ({
	dataQA,
	className,
	variant = "Avatar",
	icon,
	avatarText,
	avatarBackgroundColor,
	strokeColor,
	imageSource,
	text,
	subtext,
	alignment = "left",
	m,
	ml,
	mr,
	mt,
	mb,
}) => (
	<StyledMediaObject
		data-qa={dataQA}
		className={className}
		m={m}
		ml={ml}
		mr={mr}
		mt={mt}
		mb={mb}
	>
		<MediaWrapper alignment={alignment}>
			{variant === "Icon" && icon && (
				<Avatar
					dataQA={`${dataQA}-icon`}
					variant="Placeholder"
					size="Large"
					icon={icon}
					color={strokeColor || `${colors.black}A8`}
					backgroundColor={avatarBackgroundColor || colors.lightGray}
				/>
			)}
			{variant === "Avatar" && (
				<Avatar
					dataQA={`${dataQA}-avatar`}
					variant={imageSource ? "Image" : "Text"}
					size="Large"
					text={avatarText}
					imageSource={imageSource}
					color={strokeColor || `${colors.black}A8`}
					backgroundColor={avatarBackgroundColor || colors.lightGray}
				/>
			)}
			{variant === "Thumbnail" && imageSource && (
				<Thumbnail data-qa={`${dataQA}-thumbnail`}>
					<img src={imageSource} alt="" />
				</Thumbnail>
			)}
		</MediaWrapper>
		<ContentWrapper alignment={alignment}>
			{ text && <Typography dataQA={`${dataQA}-text`} variant="bodyCopySmall">{text}</Typography>}
			{ subtext && (
				<Typography dataQA={`${dataQA}-subtitle`} variant="caption" color={`${colors.black}A8`}>
					{subtext}
				</Typography>
			)}
		</ContentWrapper>
	</StyledMediaObject>
);

MediaObject.displayName = "MediaObject";
export default MediaObject;
